.border-ani {
  display: block;
  text-align: center;
  text-decoration: none;
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
}
.border-ani:before,
.border-ani:after {
  position: absolute;
  content: "";
  transition: all 2s;
}
.border-ani:before {
  border-bottom: 4px solid #242424;
  border-left: 4px solid #242424;
  width: 10%;
  height: 33%;
  left: 0px;
  bottom: 0px;
}
.border-ani:after {
  border-top: 4px solid #242424;
  border-right: 4px solid #242424;
  width: 10%;
  height: 33%;
  top: 0px;
  right: 0px;
}
.border-ani.go:before {
  width: 100%;
  height: 100%;
}
.border-ani.go:after {
  width: 100%;
  height: 100%;
}
